// ==============================================
//             Extremes Colors
// ==============================================
$black      : #222222;
$white      : #FFFFFF;
$grey-light : #F4F5F6;
$transparent: transparent;
$niceWhiteTransparent: rgba(254,254,254, .2);

// ==============================================
//             Primary Colors
// ==============================================
$blue  : #30499B;
$red   : #EE4035;
$yellow: #F0A32F;

// ==============================================
//            Secondary Colors
// ==============================================
$green : #56B949;
$peach : #FFB98D;
$purple: #844D9E;

.niceMarry {
  @include grandent-background($blue, $purple); }

.niceSunThree {
  @include grandent-background($green, $peach); }

.niceCoral {
  @include grandent-background($green, $blue); }

.niceBluePeach {
  @include grandent-background($blue, $peach); }

.niceBluePeachDark {
  @include grandent-background(darken($blue, 25%), $peach); }

.niceSundown {
  @include grandent-background($blue, $red); }

.niceRose {
  @include grandent-background($red, lighten($red, 25%)); }

.niceRealPeach {
  @include grandent-background($red, $peach); }

.niceBlue {
  @include grandent-background($blue, darken($blue, 25%)); }
