.s-list {
  display: block;
  width: 100%;
  font-weight: $font-light; }

.s-list__item {
  display: flex;
  width: 100%;
  color: $black;
  border-top: 1px solid $grey-light;
  border-left: 1px solid $white;
  padding: $default-padding $default-padding*2;
  text-decoration: none;
  box-sizing: border-box;
  @include niceTransition(background, border-color);

  &:active {
    background: darken($white, 5%); }

  &:hover {
    background: darken($grey-light, 2%);
    border-left-color: darken($grey-light, 2%); } }

@include x-list-item("primary", $color-primary);
@include x-list-item("secondary", $color-secondary);
@include x-list-item("danger", $color-error);
@include x-list-item("warning", $color-warning);
@include x-list-item("success", $color-success);
@include x-list-item("black", $black);

.s-list__item--disabled {
  background: darken($white, 15%);
  cursor: not-allowed;
  color: darken($white, 50%);

  &:hover {
    background: darken($white, 15%); } }

.s-list__header {
  display: block;
  font-size: $h6-size;
  background: darken($white, 5%);
  text-transform: uppercase;
  width: 100%;
  padding: $default-padding/2 $default-padding*2;
  box-sizing: border-box; }

@include x-list-header("primary", $color-primary, $white);
@include x-list-header("secondary", $color-secondary, $white);
@include x-list-header("danger", $color-error, $white);
@include x-list-header("warning", $color-warning, $white);
@include x-list-header("success", $color-success, $white);
@include x-list-header("black", $black, $white);
