@mixin grandent-background($fist-color, $last-color) {
  $angle: 135deg;
  background-image: -webkit-linear-gradient($angle, $fist-color, $last-color) !important;
  background-image: -moz-linear-gradient($angle, $fist-color, $last-color) !important;
  background-image: -o-linear-gradient($angle, $fist-color, $last-color) !important;
  background-image: linear-gradient($angle, $fist-color, $last-color) !important; }

@mixin niceTransition($properties...) {
  transition-property: $properties;
  transition-duration: $time; }

@mixin cleafix() {
  content: '';
  display: table;
  clear: both; }

@mixin vendor($props, $properties...) {
  -webkit-#{$props}: $properties;
  -moz-#{$props}: $properties;
  #{$props}: $properties; }

@mixin box-shadow($inset, $hoff, $blur, $color) {
  -webkit-box-shadow: $inset $hoff $blur $color;
  -moz-box-shadow: $inset $hoff $blur $color;
  box-shadow: $inset $hoff $blur $color; }

@mixin border-radius($properties...) {
  -webkit-border-radius: $properties;
  -moz-border-radius: $properties;
  border-radius: $properties; }

@mixin flexbox() {
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex; }

@mixin rise() {
  @include box-shadow($shadowInset, $shadowCardHoff, $shadowBlur, darken($grey-light, 15%)); }
