@mixin x-button($name, $color, $font-color) {
  .s-button--#{$name} {
    background-color: $color;
    border-color: $color;
    color: $font-color;
    &:hover {
      color: $font-color;
      background: lighten($color, 15%);
      border-color: lighten($color, 15%); }

    &:active {
      background: darken($color, 15%);
      border-color: darken($color, 15%); }

    &:focus {
      @include box-shadow($shadowInset, $shadowHoff, $shadowBlur, $color); } } }

@mixin x-card($name, $color) {
  .s-card--#{$name} {
    border-color: $color; } }

@mixin x-field($name, $color) {
  .s-field--#{$name} {
    border-color: lighten($color, 25%);

    &:hover {
      border-color: lighten($color, 15%); }

    &:focus {
      border-color: $color; } } }

@mixin x-list-header($name, $color, $font-color) {
  .s-list__header--#{$name} {
    color: $font-color;
    background-color: $color; } }

@mixin x-list-item($name, $color) {
  .s-list__item--#{$name} {
    border-top-color: transparentize($color, 0.8);
    border-left-color: $color;
    &:hover {
      border-left-color: $color;
      background-color: transparentize($color, 0.8); } } }

